body {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  min-width: 320px;
  margin: 0;
  padding: 0;
}
ul {
  padding: 0;
  list-style: none;
}
img {
  display: block;
  width: 100%;
  height: auto;
}
.button {
  display: block;
  background-color: #68b738;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  padding: 11px 0;
  color: #ffffff;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.catalog__title {
  font-size: 36px;
  line-height: 60px;
  position: relative;
  font-weight: normal;
}
.catalog__list {
  list-style: none;
  display: flex;
  flex-direction: column;
}
.catalog__item {
  margin-bottom: 21px;
  position: relative;
  /*border-bottom: 1px solid #ededed;

  &:first-child {
    border-top: 1px solid #ededed;
    padding-top: 20px;
  }*/
}
.catalog__item::before {
  content: "";
  position: absolute;
  top: -20px;
  left: -20px;
  width: 100vw;
  height: 1px;
  background-color: #ededed;
}
.catalog-card {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.catalog-card--more br {
  display: none;
}
.catalog-card__right-column {
  text-align: right;
}
.catalog-card--more {
  justify-content: center;
}
.catalog-card__table {
  border-collapse: collapse;
  width: 140px;
}
.catalog-card__table br {
  display: none;
}
.catalog-card__title {
  text-transform: uppercase;
  width: 75px;
  text-align: left;
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 15px;
}
.catalog-card__img {
  width: 140px;
  height: 144px;
  /*background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -20px;*/
}
