.example {
  background-color: #f2f2f2;
  padding: 15px 0 30px;
}
.example__tabs {
  margin: 0 auto;
}
.example__image {
  width: 320px;
  height: 240px;
  display: block;
  order: -1;
  margin-top: 40px;
  margin-bottom: 40px;
  margin-left: -60px;
}
.example__image--after {
  display: none;
}
.example__title {
  font-weight: normal;
  font-size: 36px;
  line-height: 60px;
  margin: 0;
}
.example__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  margin-bottom: 25px;
}
.example__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.example__results {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.example__result {
  box-sizing: border-box;
  width: 125px;
  text-align: center;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
  /*padding: 16px 0;*/
  border: 1px solid #cdcdcd;
  border-radius: 5px;
  transform: rotate(180deg) scaleX(-1);
}
.example__result span {
  display: block;
  transform: rotateX(180deg);
}
.example__total {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  text-transform: none;
  color: #444444;
  vertical-align: bottom;
  transform: rotate(180deg) scaleX(-1);
}
.example__cost {
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0 auto;
  margin-top: 20px;
}
.example__switcher {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 200px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
.example__slider {
  box-sizing: border-box;
  position: relative;
  width: 85px;
  height: 14px;
  border: 1px solid #cdcdcd;
  border-radius: 5px;
  background-color: #ffffff;
}
.example__slider span {
  position: absolute;
  display: block;
  top: 50%;
  left: 5px;
  width: 35px;
  height: 6px;
  background-color: #68b738;
  border-radius: 5px;
  margin-top: -3px;
  transition: 0.4s;
}
.example__switcher input {
  display: none;
}
.example__switcher input:checked + .example__slider span {
  transform: translateX(39px);
}
.example__switcher input:checked ~ .example__image--before {
  display: none;
}
.example__switcher input:checked ~ .example__image--after {
  display: block;
}
.example__comparison {
  display: none;
  padding-bottom: 20px;
  margin-top: 250px;
  background: linear-gradient(to right, #f2f2f2 49%, #eaeaea 49%);
}
.example__photos {
  position: relative;
  width: 690px;
  min-height: 460px;
  margin: 0 auto;
}
.example__photo-before,
.example__photo-after {
  position: absolute;
  top: -167px;
}
.example__photo-before {
  z-index: 20;
  clip: rect(auto, 340px, auto, auto);
}
.example__photo-after {
  clip: rect(auto, auto, auto, 340px);
}
