.example__range {
  position: absolute;
  top: 400px;
  left: 55px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 570px;
}
.example__scale {
  position: relative;
  width: 425px;
  height: 6px;
  background-color: #dcdcdc;
  border-radius: 5px;
}
.example__toggle {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -17px;
  margin-left: -17px;
  width: 34px;
  height: 34px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
  background-color: #ffffff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.15);
  transition: 0.3s;
}
.example__toggle:hover {
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.25);
}
.example__toggle:active {
  border-color: #68b738;
}
.example__toggle::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  top: 50%;
  left: 50%;
  margin-top: -4px;
  margin-left: -4px;
  background-color: #68b738;
  border-radius: 50%;
}
.example__button {
  font-size: 20px;
